import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    todos: []
  },
  mutations: {
    ADD_TODO:(state,todo) => {
      state.todos.push(todo)
      return true
    },
    DELETE_TODO(state,index){
      state.todos.splice(index,1)
    },
    DONE_TODO(state,todo){
      let index = state.todos.indexOf(todo)
      let mtodo = state.todos.find(td => state.todos.indexOf(td) === index)
      mtodo.done = !mtodo.done
      
      
    }
  },
  actions: {
    addTodo(context,todo){
      return context.commit('ADD_TODO',todo)
    },
    deleteTodo(context,index){
      context.commit('DELETE_TODO',index)
    },
    doneTodo(context,index){
      context.commit('DONE_TODO',index)
    }
  },
  getters:{
    todos: (state) => state.todos
  }
})
