<template>
    <div>
    
        <p v-if="error" style="color:red">Please fill all fields</p>
        <md-field>
            <label>Todo Body</label>
            <md-input  v-model="todo.name"></md-input>
        </md-field>
        <md-field>
             <label>Timer in Minutes for example (0.1 min = 6 seconds)</label>
            <md-input v-model="todo.timer" type="number"></md-input>
        </md-field>
        <md-button class="md-raised md-primary" @click="add">Add</md-button>
    </div>
</template>
<script>
import {mapActions} from 'vuex'
export default {
    data(){
        return {
            todo:{
                name: '',
                timer: 0,
                done:false
            },
            error: false
        }
    },
    methods: {
        ...mapActions({
            addTodo: 'addTodo'
        }),
        add(){
            const td = Object.assign({},this.todo)
            if(this.todo.name && this.todo.timer)
                {
                if(this.addTodo(td))
                    {
                    this.todo.name = ""
                    this.todo.timer = 0
                    this.error = false
                    }
                }
            else{
                this.error = true
            }
        }
    }
}
</script>