<template>
   <div>
    <md-dialog-alert
      :md-active.sync="alert"
      md-title="Time is up!"
      md-content="Did you finish in time?." />
        <div v-if="!todo.done" class="layout">
        <div class="md-title md-layout-item">{{todo.name}}  ðŸ•’ {{todo.timer}} Minutes  </div>
        <div class="md-title md-layout-item">Current Time {{current}}</div>
        <div class="md-layout-item">
        <md-button @click="startTimer">
            <md-tooltip md-direction="left" >Start Timer</md-tooltip>
            <i class="larg material-icons">launch</i>
        </md-button>
        <md-button @click.prevent="finishTodo">
            <md-tooltip md-direction="bottom">Finish Todo</md-tooltip>
            <i class="larg material-icons">done</i>
        </md-button>
        <md-button @click.prevent="delTodo">
            <md-tooltip md-direction="right">Delete Todo</md-tooltip>
            <i class="larg material-icons">delete_forever</i>
        </md-button>
        </div>
    </div>
    <div class="layout" v-else>
        <span class="md-title done">{{todo.name}}  ðŸ•’ {{todo.timer}} Minutes </span>
        <div class="secondary-content">
        <md-button @click.prevent="finishTodo" >
            <md-tooltip md-direction="right">Reset Todo</md-tooltip>
            <i class="larg material-icons">autorenew</i>
        </md-button>
        <md-button @click.prevent="delTodo">
            <md-tooltip md-direction="right">Delete Todo</md-tooltip>
            <i class="larg material-icons">delete_forever</i>
        </md-button>
        </div>
    </div>
   
   </div>
</template>
<script>
import {mapActions} from 'vuex'
export default {
    props:['todo','index'],
    data() {
        return {
            alert:false,
            current:""
        }
    },
    methods: {
        ...mapActions({
            deleteTodo: 'deleteTodo',
            doneTodo:'doneTodo'
        }),
        startTimer(){
            let seconds = this.todo.timer * 60
            let intr = setInterval(() => {
                seconds--
                this.current = (seconds/60-(seconds/60 % 1))+":"+(seconds % 60)
                if(seconds == 0){
                    var sound = new Howl({
                    src: ['point-blank.mp3']
                    });

                    sound.play();
                    this.alert = true
                    return clearInterval(intr)
                }
            },1000)
            
            
        },
        finishTodo(){
            this.doneTodo(this.todo)
        },
        delTodo(){
            this.deleteTodo(this.index)
        }
    },
}
</script>
<style scoped>
.done {
    text-decoration: line-through
}
.layout {
    position: absolute;
    top:0px;
    display: flex;
    justify-content: space-around;
      flex-wrap: wrap;
      align-content: stretch;
    width: 100%
}
</style>